.CodeMirror .cm-spell-error:not(.cm-url):not(.cm-comment):not(.cm-tag):not(.cm-word) {
  background: none !important;
}
.article-detail {
  margin: 0 auto;
  font-family: 'Lato', 'PingFang SC', 'Microsoft YaHei', sans-serif;
  color: #555;
  line-height: 2;
}
.article-detail img {
  max-width: 100%;
  margin: 0 auto 25px;
  box-sizing: border-box;
  padding: 3px;
  border: 1px solid #ddd;
}
.article-detail pre,
.article-detail code {
  font-family: consolas, Menlo, 'PingFang SC', 'Microsoft YaHei', monospace;
}
.article-detail code {
  padding: 2px 4px;
  word-wrap: break-word;
  color: #ff502c;
  background: #fff5f5;
  border-radius: 3px;
  font-size: 13px;
}
.article-detail pre {
  padding: 10px;
  overflow: auto;
  margin: 20px 0;
  font-size: 13px;
  color: #4d4d4c;
  background: #f7f7f7;
  line-height: 1.6;
}
.article-detail pre code {
  padding: 0;
  color: #555;
  background: none;
  text-shadow: none;
  font-family: consolas, Menlo, 'PingFang SC', 'Microsoft YaHei', monospace;
}
.article-detail h2,
.article-detail h3,
.article-detail h4,
.article-detail h5,
.article-detail h6 {
  margin: 20px 0 15px 0;
  padding-top: 10px;
  border-bottom: 1px solid #eee;
  margin-bottom: 10px;
  font-weight: bold;
  line-height: 1.5;
  font-family: 'Lato', 'PingFang SC', 'Microsoft YaHei', sans-serif;
  color: #555;
}
.article-detail h2 {
  font-size: 1.4em;
}
.article-detail h3 {
  font-size: 1.3em;
  border-bottom: 1px dotted #eee;
}
.article-detail h4 {
  font-size: 1.2em;
}
.article-detail ul {
  padding-left: 20px;
}
.article-detail ul li {
  line-height: 2;
  list-style: circle;
}
.article-detail .hljs-comment,
.article-detail .hljs-quote {
  color: #575f6a;
}
.article-detail blockquote {
  margin: 1em 0;
  border-left: 4px solid #ddd;
  padding: 0 1em;
  color: #666;
}
.article-detail blockquote p {
  margin: 0.5em 0;
  line-height: 1.7em;
}
.article-detail table {
  font-size: 0.8em;
  max-width: 100%;
  overflow: auto;
  border: 1px solid #f6f6f6;
  border-collapse: collapse;
  border-spacing: 0;
}
.article-detail table thead {
  background: #f6f6f6;
  color: #000;
  text-align: left;
}
.article-detail table tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}
.article-detail table tr:nth-child(2n) {
  background-color: #fcfcfc;
}
.article-detail table th {
  padding: 0.8em 0.5em;
  line-height: 1.5em;
}
.article-detail table tbody {
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
}
.article-detail table tbody td {
  min-width: 7.5em;
  padding: 0.8em 0.5em;
  line-height: 1.5em;
}
body {
  position: relative;
  font-family: 'Chinese Quote', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  font-size: 14px;
  line-height: 2;
  color: #555;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: bold;
  line-height: 1.5;
  font-family: 'Lato', 'PingFang SC', 'Microsoft YaHei', sans-serif;
}
#nprogress .bar {
  background: #bada55;
}
#nprogress .peg {
  box-shadow: 0 0 10px #bada55, 0 0 5px #bada55;
}
#nprogress .spinner-icon {
  border-top-color: #bada55;
  border-left-color: #bada55;
}
.ant-back-top-icon {
  background-size: contain;
}
.drawer-btn {
  position: fixed;
  width: 41px;
  height: 41px;
  top: 100px;
  right: 0;
  cursor: pointer;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.15);
  border-radius: 4px 0 0 4px;
  text-align: center;
  line-height: 41px;
  z-index: 999;
  background: #fff;
}
@media screen and (max-width: 1300px) {
  .content-inner-wrapper {
    padding: 0 20px !important;
  }
  .ant-anchor {
    left: -20px;
  }
  .ant-anchor-ink::before {
    display: none;
  }
}
.pagination {
  text-align: right;
  margin-bottom: 20px;
}
